"use client";

import * as React from "react";
import { motion } from "framer-motion";
import {
  Settings,
  Sparkles,
  School,
  Calendar,
  Bell,
  Shield,
  Palette,
  Globe,
  Save,
  Clock,
  Database,
  Cloud,
  Trash2,
} from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
} from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { toast } from "sonner";

const container = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: {
      staggerChildren: 0.05,
    },
  },
};

const item = {
  hidden: { y: 20, opacity: 0 },
  show: { y: 0, opacity: 1 },
};

export default function SettingsPage() {
  const [isSaving, setIsSaving] = React.useState(false);

  const handleSave = () => {
    setIsSaving(true);
    setTimeout(() => {
      setIsSaving(false);
      toast.success("Settings saved successfully");
    }, 1000);
  };

  return (
    <motion.div
      variants={container}
      initial="hidden"
      animate="show"
      className="space-y-8 pb-8"
    >
      {/* Header Section */}
      <motion.div
        variants={item}
        className="relative overflow-hidden rounded-[2rem] bg-gradient-to-br from-primary/10 via-primary/5 to-background border border-primary/10 p-8 md:p-10"
      >
        <div className="relative z-10 flex flex-col md:flex-row md:items-center justify-between gap-6">
          <div className="space-y-2">
            <div className="flex items-center gap-2 text-primary">
              <Sparkles className="h-5 w-5" />
              <span className="text-sm font-bold tracking-widest uppercase">
                System Configuration
              </span>
            </div>
            <h1 className="text-4xl font-bold tracking-tight text-foreground">
              App <span className="text-primary">Settings</span>
            </h1>
            <p className="text-muted-foreground max-w-md">
              Configure your institution's profile, academic calendar, and
              system preferences.
            </p>
          </div>
          <Button
            onClick={handleSave}
            disabled={isSaving}
            className="glass-button-primary h-14 px-8 rounded-2xl shadow-xl shadow-primary/20 group"
          >
            <Save className={cn("mr-2 h-5 w-5", isSaving && "animate-spin")} />
            {isSaving ? "Saving Changes..." : "Save Settings"}
          </Button>
        </div>
        <div className="absolute top-0 right-0 -translate-y-1/2 translate-x-1/4 w-64 h-64 bg-primary/10 rounded-full blur-3xl" />
      </motion.div>

      <div className="grid gap-8 lg:grid-cols-3">
        {/* Sidebar Navigation */}
        <motion.div variants={item} className="lg:col-span-1 space-y-2">
          {[
            { icon: School, label: "Institution Profile", active: true },
            { icon: Calendar, label: "Academic Year" },
            { icon: Bell, label: "Notifications" },
            { icon: Palette, label: "Appearance" },
            { icon: Shield, label: "Security & Privacy" },
            { icon: Database, label: "Data Management" },
          ].map((nav, i) => (
            <Button
              key={i}
              variant="ghost"
              className={cn(
                "w-full justify-start h-12 rounded-xl px-4 font-bold transition-all duration-300",
                nav.active
                  ? "bg-primary/10 text-primary shadow-sm"
                  : "text-muted-foreground hover:bg-primary/5 hover:text-primary"
              )}
            >
              <nav.icon className="mr-3 h-5 w-5" />
              {nav.label}
            </Button>
          ))}
        </motion.div>

        {/* Settings Content */}
        <motion.div variants={item} className="lg:col-span-2 space-y-8">
          {/* Institution Profile */}
          <Card className="rounded-[2rem] border-primary/5 bg-card/50 backdrop-blur-sm overflow-hidden">
            <CardHeader className="p-8 pb-4">
              <div className="flex items-center gap-3 mb-2">
                <div className="h-10 w-10 rounded-xl bg-primary/10 flex items-center justify-center text-primary">
                  <School className="h-5 w-5" />
                </div>
                <CardTitle className="text-2xl font-bold">
                  Institution Profile
                </CardTitle>
              </div>
              <CardDescription>
                Update your school's basic information and branding.
              </CardDescription>
            </CardHeader>
            <CardContent className="p-8 pt-4 space-y-6">
              <div className="grid gap-6 md:grid-cols-2">
                <div className="space-y-2">
                  <Label
                    htmlFor="schoolName"
                    className="text-xs font-bold uppercase tracking-wider text-muted-foreground"
                  >
                    School Name
                  </Label>
                  <Input
                    id="schoolName"
                    defaultValue="St. Mary's Academy"
                    className="h-12 rounded-xl bg-background/50 border-primary/5"
                  />
                </div>
                <div className="space-y-2">
                  <Label
                    htmlFor="schoolEmail"
                    className="text-xs font-bold uppercase tracking-wider text-muted-foreground"
                  >
                    Contact Email
                  </Label>
                  <Input
                    id="schoolEmail"
                    defaultValue="admin@stmarys.edu"
                    className="h-12 rounded-xl bg-background/50 border-primary/5"
                  />
                </div>
              </div>
              <div className="space-y-2">
                <Label
                  htmlFor="address"
                  className="text-xs font-bold uppercase tracking-wider text-muted-foreground"
                >
                  Address
                </Label>
                <Input
                  id="address"
                  defaultValue="123 Education Lane, Academic District"
                  className="h-12 rounded-xl bg-background/50 border-primary/5"
                />
              </div>
            </CardContent>
          </Card>

          {/* Academic Calendar */}
          <Card className="rounded-[2rem] border-primary/5 bg-card/50 backdrop-blur-sm overflow-hidden">
            <CardHeader className="p-8 pb-4">
              <div className="flex items-center gap-3 mb-2">
                <div className="h-10 w-10 rounded-xl bg-primary/10 flex items-center justify-center text-primary">
                  <Calendar className="h-5 w-5" />
                </div>
                <CardTitle className="text-2xl font-bold">
                  Academic Calendar
                </CardTitle>
              </div>
              <CardDescription>
                Manage terms, semesters, and holiday schedules.
              </CardDescription>
            </CardHeader>
            <CardContent className="p-8 pt-4 space-y-6">
              <div className="grid gap-6 md:grid-cols-2">
                <div className="space-y-2">
                  <Label className="text-xs font-bold uppercase tracking-wider text-muted-foreground">
                    Current Semester
                  </Label>
                  <Select defaultValue="fall-2025">
                    <SelectTrigger className="h-12 rounded-xl bg-background/50 border-primary/5">
                      <SelectValue placeholder="Select semester" />
                    </SelectTrigger>
                    <SelectContent className="rounded-xl">
                      <SelectItem value="fall-2025">Fall 2025</SelectItem>
                      <SelectItem value="spring-2026">Spring 2026</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div className="space-y-2">
                  <Label className="text-xs font-bold uppercase tracking-wider text-muted-foreground">
                    Timezone
                  </Label>
                  <Select defaultValue="utc-4">
                    <SelectTrigger className="h-12 rounded-xl bg-background/50 border-primary/5">
                      <SelectValue placeholder="Select timezone" />
                    </SelectTrigger>
                    <SelectContent className="rounded-xl">
                      <SelectItem value="utc-4">
                        Eastern Time (UTC-4)
                      </SelectItem>
                      <SelectItem value="utc-7">
                        Pacific Time (UTC-7)
                      </SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Preferences */}
          <Card className="rounded-[2rem] border-primary/5 bg-card/50 backdrop-blur-sm overflow-hidden">
            <CardHeader className="p-8 pb-4">
              <div className="flex items-center gap-3 mb-2">
                <div className="h-10 w-10 rounded-xl bg-primary/10 flex items-center justify-center text-primary">
                  <Settings className="h-5 w-5" />
                </div>
                <CardTitle className="text-2xl font-bold">
                  Preferences
                </CardTitle>
              </div>
              <CardDescription>
                Customize your system experience.
              </CardDescription>
            </CardHeader>
            <CardContent className="p-8 pt-4 space-y-6">
              <div className="flex items-center justify-between p-4 rounded-2xl bg-background/50 border border-primary/5">
                <div className="space-y-0.5">
                  <Label className="text-sm font-bold">Auto-save Changes</Label>
                  <p className="text-xs text-muted-foreground">
                    Automatically save your work as you type.
                  </p>
                </div>
                <Switch defaultChecked />
              </div>
              <div className="flex items-center justify-between p-4 rounded-2xl bg-background/50 border border-primary/5">
                <div className="space-y-0.5">
                  <Label className="text-sm font-bold">
                    Email Notifications
                  </Label>
                  <p className="text-xs text-muted-foreground">
                    Receive weekly timetable summaries via email.
                  </p>
                </div>
                <Switch defaultChecked />
              </div>
              <div className="flex items-center justify-between p-4 rounded-2xl bg-background/50 border border-primary/5">
                <div className="space-y-0.5">
                  <Label className="text-sm font-bold">Public Timetable</Label>
                  <p className="text-xs text-muted-foreground">
                    Allow students to view timetables without logging in.
                  </p>
                </div>
                <Switch />
              </div>
            </CardContent>
          </Card>

          {/* Danger Zone */}
          <Card className="rounded-[2rem] border-destructive/10 bg-destructive/5 overflow-hidden">
            <CardHeader className="p-8 pb-4">
              <div className="flex items-center gap-3 mb-2">
                <div className="h-10 w-10 rounded-xl bg-destructive/10 flex items-center justify-center text-destructive">
                  <Trash2 className="h-5 w-5" />
                </div>
                <CardTitle className="text-2xl font-bold text-destructive">
                  Danger Zone
                </CardTitle>
              </div>
              <CardDescription>
                Critical actions that cannot be undone.
              </CardDescription>
            </CardHeader>
            <CardContent className="p-8 pt-4">
              <Button
                variant="destructive"
                className="h-12 px-6 rounded-xl font-bold shadow-lg shadow-destructive/20"
              >
                Reset All Timetable Data
              </Button>
            </CardContent>
          </Card>
        </motion.div>
      </div>
    </motion.div>
  );
}

function cn(...inputs: any[]) {
  return inputs.filter(Boolean).join(" ");
}
